.order-items {
  max-height: 70vh;
  overflow-y: auto;
  list-style: none;
  padding: 0 1rem;
}

.order-item {
  border-bottom: 0.15rem solid green;
}

.order-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 0;
}

.order-content div:first-child {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
}

.amount,
.price {
  color: green;
  font-size: larger;
  font-weight: bold;
}

@media screen and (max-width: 630px) {
  .order-content div:first-child {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 85%;
  }
}
